@import 'vars';
@import 'mixins';

.#{$prefix}card--register,
.#{$prefix}card--login,
.#{$prefix}card--forgot-password {
  position: absolute;
  z-index: 1;
  width: 268px;
  top: 60px;
  left: 50%;
  transform: translate(-50%, 0);

  @include mobile {
    top: 10px;
    width: 100%;
    max-width: 460px; } }

.#{$prefix}card--forgot-password {
  padding-bottom: 14px;

  .#{$prefix}copy--center {
    @include mobile {
      font-size: 14px; } } }


.#{$prefix}copy--need-an-account {
  display: none;
  @include mobile {
    display: block; } }
